/**
 * The following styles get applied both on the front of your site
 * and in the editor.
 *
 * Replace them with your own styles or remove the file completely.
 */
:root {
	--blockHeight: 250px;
	--person_padding: 2rem;
}

.flexContainerNew {
	min-width: 600px;
	max-width: 52rem;
	@media (max-width: 650px) {
		flex-direction: column;
		height: auto;
		text-align: center;
	}
	display: flex;
	flex-direction: row;
	align-items: center;
	position: relative;
	padding: 0rem 5rem;
	margin-bottom: -1rem;
	gap: var(--person_padding);
	justify-content: center;
	@media (min-width: 1110px) {
	}
	transition: 1s;
	&:hover {
		transform: scale(1.1);
		filter: drop-shadow(5px 5px 10px rgba(0, 0, 0, 0.238));
	}
}

.outerContainer {
	width: 1330px !important;
}

.outerOuterContainer {
	@media (max-width: 650px) {
		margin-bottom: 2rem;
	}
	width: 100%;
	display: flex;
	justify-content: center;
}

.flexContainerRight {
	@media (max-width: 650px) {
		flex-direction: column-reverse;
	 }
}

.blobMask {
	mask: url(blob.svg);
	mask-size: 100% 100%;
}

.moveRight {
	display: flex;
	width: 100%;
	justify-content: end;
}

.blobMaskInverted {
	mask: url(invertedBlob.svg);
	width: 100%;
	height: 100%;
	mask-size: 100% 100%;
	position: relative;
}

.textRightContainer {
	width: 520px;
	max-width: initial !important;
	@media (max-width: 650px) {
		flex-direction: column-reverse !important;
	}
	@media (min-width: 650px) {
		padding: 7rem 6rem 6rem 6rem !important;
		margin-right: -7rem;
		padding-right: 6rem;
	 }
}

.imageContainer {
	width: calc(var(--blockHeight) - var(--person_padding) * 2);
	height: calc(var(--blockHeight) - var(--person_padding) * 2);
	position: relative;
	img {
		height: 100%;
		width: 100%;
		border-radius: 50%;
		object-fit: cover;
	}
	z-index: 2;
}

.textContainer {
	z-index: 1 !important;
	padding: 7rem 4rem;
	padding-top: 136px;
	@media (max-width: 650px) {
		margin-top: -8rem;
		max-width: calc(100% - var(--person_padding));
	}
	// max-width: calc(100% - var(--blockHeight) + var(--person_padding));
	.lowerText {
		color: #BF4B62;
		margin: 0;
		@media (max-width: 650px) {
			text-align: center;
		}
	}
	.upperText {
		@media (min-width:650px) {
			width: 322px;
		}
		@media (max-width: 650px) {
			text-align: center;
		}
	}
	.upperTextRight {
		@media (max-width: 650px) {
			text-align: center;
		}
	}
	box-shadow: 5px 5px -5px 10px black;
}

.textLeftContainer {
	@media (min-width: 650px) {
		padding: 3rem 4rem !important;
		margin-top: 20px;
		margin-left: -5rem;
		padding-left: 6rem;
		text-align: initial !important;
	 }
	 padding: 4rem !important;
	 @media (max-width: 650px) {
		 margin-top: -5rem !important;
	 }
}

.person_nameRed {
	color: #BF4B62 !important;
}
.person_bgRed {
	background: #F6E9EF !important;
}
.person_nameYellow {
	color: #BF4B62 !important;
}
.person_bgYellow {
	background: #FDF6D1 !important;
}
.person_nameGreen {
	color: #04395E !important;
}
.person_bgGreen {
	background: #E3F5D6 !important;
}

.textRightName {
	text-align: right;
	@media (max-width: 650px) {
		text-align: center;
	}
}

.textRightP {
text-align: right;
@media (max-width: 650px) {
	text-align: center;
}
}
